.flex {
  display: flex;
  flex-direction: column;
}

.formControls {
  height: 260px;
  font-size: 12px;
  margin: 5px;
}

.success {
  color: green;
  font-weight: bold;
  height: 20px;
}

.row {
  height: 45px;
}

.labelRow {
  flex-direction: row;
  height: 25px;
}

.label {
  width: 80px;
  justify-content: center;
  align-items: flex-start;
  margin-left: 10px;
}

.input {
  flex-grow: 1;
}

.errorMessage {
  color: red;
}

.submitRow {
  align-items: center;
}

.submit {
  background-color: gray;
  width: 100px;
}
