.flex {
  display: flex;
  flex-direction: column;
}

.navbar {
  background-color: blue;
  height: 30px;
  width: 100%;
}

.container {
  text-align: center;
  width: 100%;
  max-width: 800px;
  flex: 1;
}

.top {
  flex-direction: row;
  flex-wrap: wrap-reverse;
  align-items: stretch;
}

.controls {
  background-color: yellow;
  justify-content: space-between;
  flex: 1;
  min-width: 300px;
}

.ballance {
  background-color: blue;
  height: 30px;
}

.form {
  background-color: rgba(22,190,255,0.35);
  flex: 1;
  min-width: 300px;
}

.footer {
  background-color: pink;
  height: 30px;
  width: 100%;
}
