.transactions {
  flex-grow: 1;
}

.transactionItem {
  height: 100px;
  padding: 5px;
  background-color: aquamarine;
  margin-bottom: 5px;
}

.row1 {
  display: flex;
  flex-direction: row;
  min-width: 300px;
}

.id {
  width: 30px;
}

.beneficiary {
  width: 200px;
  text-align: left;
  font-weight: bold;
  overflow: hidden;
  min-width: 0;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.account, .address, .description {
  width: 100%;
  text-align: left;
  overflow: hidden;
  min-width: 0;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.account {
  margin-bottom: 10px;
}

.remove {
  display: flex;
  cursor: pointer;
  flex-grow: 1;
  align-items: flex-end;
  justify-content: flex-end;
  font-weight: bold;
}
